<template>
  <h1>Diabetes en México</h1>
  <p class="parrafo_intro">
    En el país no hay un registro exacto de este padecimiento, pero se calcula
    que uno de cada diez mexicanos de 20 años y más viven con esta enfermedad.
  </p>
  <p>
    Da <span class="highlight-instrucciones"> clic </span> en los rectángulos
    con color sólido en las columnas del centro y de la derecha para obtener más
    información. Da <span class="highlight-instrucciones"> clic </span> en la
    columna de la izquierda para regresar al nivel de desagregación previo. Para
    una mejor visualización de la siguiente gráfica, se recomienda colocar el
    celular de forma horizontal
  </p>
  <div class="contenedor-titulos">
    <div class="titulo">
      <p>{{ titulo_1 }}</p>
    </div>
    <div class="titulo">
      <p>{{ titulo_2 }}</p>
    </div>
    <div class="titulo">
      <p>{{ titulo_3 }}</p>
    </div>
  </div>
  <div class="contenedor-sunburst" id="contenedor-sunburst">
    <svg
      class="zoomable-sunburst"
      :width="ancho - margenes.derecha - margenes.izquierda"
      :height="alto_svg"
    ></svg>
  </div>
  <p class="nota">
    Nota: los datos mostrados son aproximaciones hechas a partir de la ENSANUT
    2022
  </p>
</template>

<script setup>
import { ref, onMounted } from "vue";
import * as d3 from "d3";

const data = ref({
  name: "Poblacion",
  children: [
    { name: "Sin diabetes", value: 63903932.28 },
    {
      name: "Con diabetes",
      children: [
        { name: "No tenían diagnóstico previo", value: 4530303.11 },
        {
          name: "Tenían diagnóstio previo",
          children: [
            { name: "No toma medicamentos", value: 885664.19 },
            { name: "Toma únicamente pastillas", value: 6380227.93 },
            { name: "Toma pastillas e insulina", value: 1414284.24 },
            {
              name: "Toma únicamente insulina",
              children: [
                {
                  name: "No suspendió la toma del medicamento",
                  value: 883982.56,
                },
                {
                  name: "Suspendió la toma del medicamento",
                  children: [
                    {
                      name: "Teme sobre la seguridad del medicamento",
                      value: 75193.0,
                    },
                    { name: "No le surtieron el medicamento", value: 47145.44 },
                    { name: "No tuvo dinero", value: 112269.32 },
                    {
                      name: "Otros",
                      value: 25670.78,
                    },
                  ],
                },
              ],
            },
          ],
        },
      ],
    },
  ],
});

const titulos = ref({
  0: "Poblacion de 20 años y más",
  1: "Estatus diabetes",
  2: "Estatus del diagnóstico",
  3: "Tratamiento",
  4: "Suspension del medicamento más de una vez a la semana en los últimos 6 meses",
  5: "Causa de la suspensión",
});

const contenedor_svg = ref();
const svg = ref();

const alto = ref();
const alto_svg = ref();
const ancho = ref();
const margenes = ref({
  derecha: 5,
  izquierda: 5,
  abajo: 20,
  arriba: 20,
});

const color = ref();
const hierarchy = ref();
const root = ref();
const focus = ref();

const celda = ref();
const rectangulo = ref();
const text = ref();
const tspan = ref();

const titulo_1 = ref(titulos.value[0]);
const titulo_2 = ref(titulos.value[1]);
const titulo_3 = ref(titulos.value[2]);

const format = ref();

function rectHeight(d) {
  return d.x1 - d.x0 - Math.min(1, (d.x1 - d.x0) / 2);
}

function labelVisible(d) {
  return d.y1 <= ancho.value && d.y0 >= 0 && d.x1 - d.x0 > 16;
}

function clicked(event, p) {
  // p indica el elemento en el que se hizo click
  //console.log(p);
  if (p.depth == 0 /* || p.height == 0 */) {
    console.log("No se puede entrar o regresar otro nivel");
  } else {
    //console.log("Se actualiza la visualización");
    // El valor de focus ayuda a establecer la data que está a la izquierda de la pantalla
    focus.value = focus.value === p ? (p = p.parent) : p;
    // Aquí lo que estamos haciendo es colocar el nombre de cada categoría
    titulo_1.value = titulos.value[focus.value.depth];
    if (focus.value.height != 0) {
      titulo_2.value = titulos.value[focus.value.depth + 1];
      titulo_3.value = titulos.value[focus.value.depth + 2];
    } else {
      titulo_2.value = "";
      titulo_3.value = "";
    }

    // Aquí lo que hacemos es sobre escribir los valores de target, para que se acomoden en la pantalla
    root.value.each(
      (d) =>
        (d.target = {
          x0: ((d.x0 - p.x0) / (p.x1 - p.x0)) * alto_svg.value,
          x1: ((d.x1 - p.x0) / (p.x1 - p.x0)) * alto_svg.value,
          y0: d.y0 - p.y0,
          y1: d.y1 - p.y0,
        })
    );

    // Con esto ya se actualiza la data en el DOM
    let t = celda.value
      .transition()
      .duration(750)
      .attr("transform", (d) => `translate(${d.target.y0},${d.target.x0})`);

    rectangulo.value.transition(t).attr("height", (d) => rectHeight(d.target));
    text.value
      .transition(t)
      .attr("fill-opacity", (d) => +labelVisible(d.target));
    tspan.value.transition(t).attr("fill-opacity", (d) => {
      //console.log(d.target);
      return labelVisible(d.target) * 0.7;
    });
  }
}
function reescalanding() {
  dimensionando();
  dibujanding();
}

function dimensionando() {
  // Ajustar las dimensiones
  if (contenedor_svg.value.clientWidth < 769) {
    alto_svg.value = 700;
  } else {
    alto_svg.value = 650;
  }

  ancho.value =
    contenedor_svg.value.clientWidth -
    margenes.value.derecha -
    margenes.value.izquierda;

  alto.value = alto_svg.value - margenes.value.arriba - margenes.value.abajo;
  //alto_svg.value + margenes.value.arriba + margenes.value.abajo;

  color.value = d3.scaleOrdinal(
    d3.quantize(
      d3.interpolate(
        "#C14440" /*"#A23E48" Rojo*/,
        "#326148" /*"#0F5257" Verde*/
      ),
      data.value.children.length // + 1
    )
  );
  //console.log(color.value("poblacion"));

  // Computar el layout
  hierarchy.value = d3
    .hierarchy(data.value)
    .sum((d) => d.value)
    .sort((a, b) => b.height - a.height || b.value - a.value);
  root.value = d3
    .partition()
    //.padding(3)
    .size([alto_svg.value, ((hierarchy.value.height + 1) * ancho.value) / 3])(
    hierarchy.value
  );
  //console.log(root.value);
  focus.value = root.value;

  // Definimos el formato, literal solo es para dar formato a los números que se muestran
  format.value = d3.format(",d");
}

function dibujanding() {
  //console.log("Se dibujó la gráfica");
  svg.value.selectAll("g").remove();

  celda.value = svg.value
    .selectAll("g")
    .data(root.value.descendants())
    .join("g")
    .attr("transform", (d) => `translate(${d.y0},${d.x0})`)
    .attr("class", (d) => d.depth);

  rectangulo.value = celda.value
    .append("rect")
    .attr("width", (d) => {
      //console.log(d);
      return d.y1 - d.y0 - 1;
    })
    .attr("height", (d) => rectHeight(d))
    .attr("fill-opacity", (d) => {
      if (d.height == 0) {
        return 0.8;
      } else {
        return 1;
      }
    })
    .attr("fill", (d) => {
      if (!d.depth) return "#141514";
      while (d.depth > 1) d = d.parent;
      return color.value(d.data.name);
    })
    .style("cursor", "pointer")
    .on("click", clicked);

  text.value = celda.value
    .append("text")
    .style("user-select", "none")
    .attr("pointer-events", "none")
    .attr("x", 4)
    .attr("y", 15)
    .attr("fill", "white")
    .attr("fill-opacity", (d) => +labelVisible(d))
    .style("font-weight", "bold");

  text.value.append("tspan").text((d) => d.data.name);

  tspan.value = text.value
    .append("tspan")
    .attr("fill", "white")
    .attr("fill-opacity", (d) => labelVisible(d) * 0.7)
    .attr("x", 4)
    .attr("y", 30)
    .text((d) => format.value(d.value));
}

onMounted(() => {
  contenedor_svg.value = document.getElementById("contenedor-sunburst");
  svg.value = d3.select("svg.zoomable-sunburst");

  //console.log(data.value);

  dimensionando();
  dibujanding();
  window.addEventListener("resize", reescalanding);
});
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
