p {
  text-align: left;
}
.nota {
  font-size: 11px;
  margin-top: 0px;
  margin-left: 10px;
}
.parrafo_intro {
  font-size: 20px;
}
.contenedor-sunburst {
  margin-bottom: 0px;
  font-size: 14px;
}

.contenedor-titulos {
  display: flex;
  /*background-color: orange; */
}
.titulo {
  width: 33%;
  font-weight: bold;
  padding: 5px;
}
.highlight-instrucciones {
  font-weight: bolder;
}
</style>
